import { getRandomNum } from './helpers/random';
import birdsDataEn from './data/birdsDataEn';
import birdsDataRu from './data/birdsDataRu';
import { shuffleArray } from './helpers/shuffle';

const getQuestionsGroup = (lang) => {
  const questionsGroup =
    lang === 'en'
      ? [
          'Warmup',
          'Passerines',
          'Forest birds',
          'Songbirds ',
          'Raptors ',
          'Seabirds',
        ]
      : [
          'Разминка',
          'Воробьиные',
          'Лесные птицы',
          'Певчие птицы',
          'Хищные птицы',
          'Морские птицы',
        ];
  return questionsGroup;
};

const chooseGameQuestions = (birdsData, group) => {
  let result = {};
  birdsData.forEach((data, i) => {
    result[group[i]] = {
      question: data[getRandomNum(0, data.length)],
    };
  });
  return result;
};

const shuffleAnswers = (birdsData) => {
  let result = [];
  birdsData.forEach((data) => result.push(shuffleArray(data)));
  return result;
};

const createGame = () => {
  const lang = localStorage.getItem('lang') || 'en';
  const birdsData = lang === 'en' ? birdsDataEn : birdsDataRu;
  const questionsGroup = getQuestionsGroup(lang);
  const newGameContent = chooseGameQuestions(birdsData, questionsGroup);
  const answers = shuffleAnswers(birdsData);
  answers.forEach(
    (data, i) => (newGameContent[questionsGroup[i]]['answers'] = answers[i])
  );
  return newGameContent;
};

export { chooseGameQuestions, createGame };
