import { showResult } from './countScore';
import { getContent } from './data/dataContent';

const START_BUTTON = document.querySelector('.button-play');
const NEXT_BUTTON = document.querySelector('.next.button');
const AGAIN_BUTTON = document.querySelector('.again.button');
const GAME_DESCRIPTION = document.querySelector('.game-description');
const SCORE = document.querySelector('.score-title');
const headerLinksEl = document.querySelectorAll('.list-item .item-link');
const questionsGroupEle = document.querySelectorAll('.questions-list-item');
const startPageText = document.querySelector('.start-app-description');

const changeContentLang = () => {
  let language = localStorage.getItem('lang') || 'en';
  let content = getContent(language);
  return content;
};
const setLangButton = (btn) => {
  let language = localStorage.getItem('lang') || 'en';
  btn.classList.contains(language) ? null : btn.classList.add(language);
};
const fillStartPage = () => {
  let [, startButton, , headerLinks, , , , startText, ,] = changeContentLang();
  startPageText.textContent = startText;
  START_BUTTON.textContent = startButton;
  headerLinksEl.forEach((link, i) => (link.textContent = headerLinks[i]));
};

const fillGamePage = () => {
  let [
    questionsGroup,
    ,
    nextButton,
    headerLinks,
    gameDescription,
    ,
    ,
    ,
    score,
  ] = changeContentLang();
  headerLinksEl.forEach((link, i) => (link.textContent = headerLinks[i]));
  questionsGroupEle.forEach(
    (question, i) => (question.textContent = questionsGroup[i])
  );
  NEXT_BUTTON.textContent = nextButton;
  GAME_DESCRIPTION.textContent = gameDescription;
  SCORE.textContent = score;
};
const fillResultPage = () => {
  let [
    ,
    ,
    ,
    headerLinks,
    ,
    congratulateText1,
    congratulateText2,
    ,
    ,
    againButton,
  ] = changeContentLang();
  headerLinksEl.forEach((link, i) => (link.textContent = headerLinks[i]));
  document.querySelector('.results-wrapper h4').textContent = congratulateText1;
  document.querySelector('.results-text').innerHTML = congratulateText2;
  showResult();
  document.querySelector('.again').textContent = againButton;
};
const changeLangImg = (e) => {
  e.target.classList.toggle('en');
  e.target.classList.toggle('ru');
  e.target.classList.contains('ru')
    ? localStorage.setItem('lang', 'ru')
    : localStorage.setItem('lang', 'en');
};
export {
  START_BUTTON,
  NEXT_BUTTON,
  AGAIN_BUTTON,
  fillStartPage,
  fillGamePage,
  fillResultPage,
  setLangButton,
  changeLangImg,
};
