const currentScore = document.querySelector('.current-score');
const questionsGroups = document.querySelectorAll('.questions-list-item');

const showScore = (maxScore) => {
  let previousScore = Number(currentScore.textContent);
  currentScore.textContent = previousScore + maxScore;
};
const showResult = () => {
  let lang = localStorage.getItem('lang');
  let resultScore = localStorage.getItem('score');
  let resultContent = document.querySelector('#results');
  let resultText = document.querySelector('.results-text');
  Number(resultScore) === 30
    ? (resultText.textContent =
        lang === 'ru'
          ? 'Вы прошли игру и набрали максимальное количество баллов'
          : 'You completed the game and scored the maximum number of points')
    : (resultContent.textContent = resultScore);
};

const changeActiveLevel = (level) => {
  questionsGroups.forEach((group) => group.classList.remove('active'));
  questionsGroups[level].classList.add('active');
};

export { showScore, changeActiveLevel, showResult };
