import { changeLevel } from '..';
import { newSound } from './audio';
import { showScore } from './countScore';
import { hiddenPlayer } from './createAudioPlayer';
import error from '../assets/sounds/error.mp3';
import correct from '../assets/sounds/correct.mp3';
import {
  chosenBirdName,
  fillCurrentAnswerInfo,
  playButton,
  playSvg,
  questionSound,
} from './fillGame';

let birdInfoBlock = document.querySelector('.chosen-bird-wrapper');
let gameInfoBlock = document.querySelector('.game-description');
const loadingContentAnswer = document.querySelector(
  '.chosen-bird-main .audio-loading'
);
const answerAudioPlayer = document.querySelector('.chosen-audio-player');

const showBirdInfo = () => {
  birdInfoBlock.classList.remove('hidden');
  gameInfoBlock.classList.add('hidden');
};
const hiddenBirdInfo = () => {
  birdInfoBlock.classList.add('hidden');
  gameInfoBlock.classList.remove('hidden');
};
const soundCheck = newSound('');
let maxScore = 5;

const correctChoice = (soundCorrect, soundBird) => {
  soundCorrect.src = correct;
  soundCorrect.play();
  soundBird.pause();
  playButton.innerHTML = playSvg;
  showScore(maxScore);
  maxScore = 5;
  changeLevel();
};

const checkAnswer = (e, currentGroup) => {
  let chosenAnswer = e.target.closest('li');
  let isRightAnswer = document.querySelector('.answer-list-item.right');
  let idRightBird = 0;
  if (Number(chosenAnswer.id) === currentGroup.question.id && !isRightAnswer) {
    chosenAnswer.classList.add('right');
    idRightBird = Number(chosenAnswer.id);
    correctChoice(soundCheck, questionSound);
  } else if (!isRightAnswer && !chosenAnswer.classList.contains('error')) {
    soundCheck.src = error;
    soundCheck.play();
    chosenAnswer.classList.add('error');
    maxScore--;
  }
  if (chosenBirdName.textContent !== chosenAnswer.textContent) {
    hiddenPlayer(loadingContentAnswer, answerAudioPlayer);
    showBirdInfo();
    fillCurrentAnswerInfo(Number(chosenAnswer.id), idRightBird, currentGroup);
  }
};

export { checkAnswer, loadingContentAnswer, answerAudioPlayer, hiddenBirdInfo };
