import './styles/style.scss';
import {
  answerSound,
  birdMainImg,
  birdMainTitle,
  fillListAnswers,
  questionSound,
} from './modules/fillGame.js';
import { createGame } from './modules/createQuestions.js';
import { checkAnswer, hiddenBirdInfo } from './modules/checkAnswers.js';
import birdDefaultImg from './assets/img/bird.jpg';
import { changeActiveLevel, showResult } from './modules/countScore';
import {
  AGAIN_BUTTON,
  NEXT_BUTTON,
  START_BUTTON,
  fillGamePage,
  fillStartPage,
  fillResultPage,
  setLangButton,
  changeLangImg,
} from './modules/renderContent';
const currentScore = document.querySelector('.current-score');
const answersBlock = document.querySelector('.answers-list');
const LANG_BUTTON = document.querySelector('#lang');
let level;

const changeLevel = () => {
  if (level < 5) {
    NEXT_BUTTON.disabled = false;
    level++;
  } else if (level === 5) {
    let score = currentScore.textContent;
    localStorage.setItem('score', score);
    window.location.href = './results.html';
  }
};
let location =
  window.location.pathname.includes('index') ||
  window.location.pathname.endsWith('/')
    ? 'start'
    : window.location.pathname.includes('play')
    ? 'play'
    : 'other';

const startApp = () => {
  setLangButton(LANG_BUTTON);
  level = 0;
  function startGame() {
    let currentGroup = '';
    const currentGameContent = createGame();
    let { warmup, passerines, forest, singing, predatory, marine } =
      currentGameContent;
    NEXT_BUTTON.disabled = true;
    currentScore.textContent = 0;

    const selectLevel = () => {
      let levelGroups = Object.keys(currentGameContent);
      currentGroup = currentGameContent[levelGroups[level]];
      questionSound.src = currentGroup.question.audio;
      fillListAnswers(currentGroup.answers);
    };

    selectLevel();

    NEXT_BUTTON.addEventListener('click', () => {
      selectLevel();

      answerSound.pause();
      NEXT_BUTTON.disabled = true;
      hiddenBirdInfo();
      birdMainTitle.textContent = '********';
      birdMainImg.style.background = `url(${birdDefaultImg}) center/cover`;
      changeActiveLevel(level);
    });
    answersBlock.addEventListener('click', (e) => checkAnswer(e, currentGroup));
  }

  if (location === 'start') {
    fillStartPage();
    START_BUTTON.addEventListener(
      'click',
      () => (window.location.href = './play.html')
    );
  }

  if (location === 'play') {
    fillGamePage();
    startGame();
  }

  if (location === 'other') {
    fillResultPage();
    showResult();
    AGAIN_BUTTON.addEventListener('click', () => {
      level = 0;
      window.location.href = './play.html';
      startGame();
    });
  }
};
startApp();

LANG_BUTTON.addEventListener('click', (e) => {
  changeLangImg(e);
  location === 'start'
    ? fillStartPage()
    : location === 'play'
    ? (window.location.href = './play.html')
    : fillResultPage();
});
export { changeLevel };
