import { newSound } from './audio';
import { answerAudioPlayer, loadingContentAnswer } from './checkAnswers';
import {
  changeDuration,
  controlVolume,
  onAudioLoad,
  toggleBtn,
  updateTime,
} from './createAudioPlayer';

const pauseSvg =
  '<svg viewBox="0 0 47.607 47.607"><path fill="#397593" d="M17.991 40.976a6.631 6.631 0 01-13.262 0V6.631a6.631 6.631 0 0113.262 0v34.345zM42.877 40.976a6.631 6.631 0 01-13.262 0V6.631a6.631 6.631 0 0113.262 0v34.345z"></path></svg>';
const playSvg = `<svg viewBox="-200 0 1200 1000">
<path
  fill="#397593"
  d="M96.51 11.97c-31.23 8.05-53.26 32.76-63.42 71.27-3.45 12.84-3.64 29.7-3.64 416.71s.19 403.87 3.64 416.71c16.09 60.74 61.69 86.03 120.9 67.25 9-2.87 53.65-25.1 116.49-58.24 56.14-29.51 221.29-116.3 367.28-192.93 145.99-76.64 271.29-143.31 278.38-148.1 39.28-25.68 59.59-63.04 53.26-97.52-4.79-26.63-24.33-53.07-52.88-71.65C892 399.37 172.58 22.32 154.95 16.38c-18.97-6.33-43.3-8.24-58.44-4.41z"></path></svg>`;
let birdMainTitle = document.querySelector('.current-bird-title');
let birdMainImg = document.querySelector('.current-bird-img');
const playButton = document.querySelector('#button-play-question');
const playButtonChosen = document.querySelector('#button-play-answer');
const answersList = document.querySelector('.answers-list');
const questionAudioCurrentTime = document.querySelector(
  '.current-time.question'
);
const questionAudioDuration = document.querySelector('.duration-time.question');

const chosenBirdImg = document.querySelector('.chosen-bird-img');
const chosenBirdName = document.querySelector('.chosen-bird-name');
const chosenBirdSpecies = document.querySelector('.chosen-bird-species');
const answerAudioCurrentTime = document.querySelector('.current-time.answer');
const answerAudioDuration = document.querySelector('.duration-time.answer');
const timebarAnswer = document.querySelector('#timebar-answer');
const timebarQuestion = document.querySelector('#timebar-question');
const chosenBirdDescription = document.querySelector(
  '.chosen-bird-description'
);
const loadingContentQuestion = document.querySelector(
  '.current-bird-audio .audio-loading'
);
const questionAudioPlayer = document.querySelector('.audio-player');
const questionVolume = document.querySelector('#questionVolume');
const answerVolume = document.querySelector('#answerVolume');
const createNewElement = (el, className) => {
  const element = document.createElement(el);
  element.classList.add(...className);
  return element;
};

let questionSound = newSound('');
let answerSound = newSound('');
let questionLoud = 0.5;
let answerLoud = 0.5;

questionSound.addEventListener(
  'loadeddata',
  () => {
    playButton.innerHTML = playSvg;
    playButton.classList.remove('pause');
    controlVolume(questionVolume, questionLoud, questionSound);
    onAudioLoad(
      questionSound,
      loadingContentQuestion,
      questionAudioPlayer,
      questionAudioDuration,
      playButton,
      timebarQuestion
    );

    timebarQuestion.addEventListener('input', (e) => {
      changeDuration(e, questionSound);
    });
    questionSound.addEventListener('timeupdate', () => {
      updateTime(questionSound, timebarQuestion, questionAudioCurrentTime);
      if (questionSound.currentTime === questionSound.duration) {
        playButton.innerHTML = playSvg;
      }
    });
  },
  false
);
!!playButton
  ? playButton.addEventListener('click', (e) =>
      toggleBtn(playButton, questionSound, e)
    )
  : null;
!!playButtonChosen
  ? playButtonChosen.addEventListener('click', (e) =>
      toggleBtn(playButtonChosen, answerSound, e)
    )
  : null;
const fillListAnswers = (list) => {
  answersList.innerHTML = '';
  let listElements = [];
  list.forEach((bird) => {
    let li = createNewElement('li', ['answer-list-item']);
    li.id = `${bird.id}`;
    let liText = createNewElement('p', ['list-item-text']);
    liText.textContent = bird.name;
    let liCheck = createNewElement('div', ['li-check']);
    li.append(liCheck, liText);
    listElements.push(li);
  });
  answersList.append(...listElements);
};

const fillIfRightAnswer = (birds, id) => {
  const bird = birds.find((item) => item.id === id);
  birdMainTitle.textContent = bird.name;
  birdMainImg.style.background = `url(${bird.image}) center/cover`;
};

const fillCurrentAnswerInfo = (id, rightId, currentGroup) => {
  let bird = currentGroup.answers.find((item) => item.id === id);
  !!rightId ? fillIfRightAnswer(currentGroup.answers, rightId) : null;
  answerSound.src = bird.audio;
  playButtonChosen.innerHTML = playSvg;
  if (playButtonChosen.classList.contains('pause')) {
    playButtonChosen.classList.remove('pause');
  }
  answerSound.pause();
  chosenBirdImg.style.background = `url(${bird.image}) center/cover`;
  chosenBirdName.textContent = bird.name;
  chosenBirdSpecies.textContent = bird.species;
  chosenBirdDescription.textContent = bird.description;
  answerSound.addEventListener(
    'loadeddata',
    () => {
      controlVolume(answerVolume, answerLoud, answerSound);
      onAudioLoad(
        answerSound,
        loadingContentAnswer,
        answerAudioPlayer,
        answerAudioDuration,
        timebarAnswer
      );
    },
    false
  );
  answerSound.currentTime = 0;
  timebarAnswer.addEventListener('input', (e) => {
    changeDuration(e, questionSound);
  });
  answerSound.addEventListener('timeupdate', () => {
    updateTime(answerSound, timebarAnswer, answerAudioCurrentTime);
    if (answerSound.currentTime === answerSound.duration) {
      playButtonChosen.innerHTML = playSvg;
    }
  });
};

export {
  playSvg,
  pauseSvg,
  fillCurrentAnswerInfo,
  answerSound,
  questionSound,
  fillListAnswers,
  birdMainTitle,
  birdMainImg,
  chosenBirdName,
  playButton,
};
