import BIRD_SOUND from '../assets/sounds/birds-singing.wav';
import DO from '../assets/sounds/do.mp3';
import RE from '../assets/sounds/re.mp3';
import MI from '../assets/sounds/mi.mp3';
import FA from '../assets/sounds/fa.mp3';
import SOLI from '../assets/sounds/soli.mp3';
import LYA from '../assets/sounds/lya.mp3';
import SI from '../assets/sounds/si.mp3';

const BIRD = document.querySelector('#bird');
const NOTE_LIST = document.querySelector('.note-list');

const newSound = (src) => {
  const audio = new Audio();
  audio.src = src;
  return audio;
};
const mainBirdAudio = newSound(BIRD_SOUND);

if (BIRD && NOTE_LIST) {
  BIRD.addEventListener('click', function () {
    mainBirdAudio.currentTime = 0;
    !BIRD.classList.contains('pause')
      ? mainBirdAudio.play()
      : mainBirdAudio.pause();
    BIRD.classList.toggle('pause');
  });

  const createNotes = [DO, RE, MI, FA, SOLI, LYA, SI].map((item) =>
    newSound(item)
  );

  NOTE_LIST.addEventListener('click', function (e) {
    switch (e.target.id) {
      case 'do':
        createNotes[0].play();
        break;
      case 're':
        createNotes[1].play();
        break;
      case 'mi':
        createNotes[2].play();
        break;
      case 'fa':
        createNotes[3].play();
        break;
      case 'sol':
        createNotes[4].play();
        break;
      case 'lya':
        createNotes[5].play();
        break;
      case 'si':
        createNotes[6].play();
        break;
    }
  });
}

export { newSound };
