import { pauseSvg, playSvg } from './fillGame';

const toggleBtn = (btn, sound, e) => {
  let target = e.target.closest('div');
  if (target) {
    target.classList.toggle('pause');
    if (target.classList.contains('pause')) {
      sound.play();
      btn.innerHTML = pauseSvg;
    } else {
      sound.pause();
      btn.innerHTML = playSvg;
    }
  }
};

const showPlayer = (loadingBox, player) => {
  loadingBox.classList.add('hidden');
  player.classList.remove('hidden');
};

const hiddenPlayer = (loadingBox, player) => {
  loadingBox.classList.remove('hidden');
  player.classList.add('hidden');
};

function getTimeCodeFromNum(num) {
  let seconds = parseInt(num);
  let minutes = parseInt(seconds / 60);
  seconds -= minutes * 60;
  const hours = parseInt(minutes / 60);
  minutes -= hours * 60;
  if (hours === 0) {
    return `${String(minutes).padStart(2, 0)}:${String(seconds % 60).padStart(
      2,
      0
    )}`;
  }
  return `${String(hours).padStart(2, 0)}:${minutes}:${String(
    seconds % 60
  ).padStart(2, 0)}`;
}

const updateTime = (sound, timebar, currentTimeContainer) => {
  const { duration, currentTime } = sound;
  timebar.value = currentTime === 0 ? 0 : currentTime / duration;
  currentTimeContainer.textContent = getTimeCodeFromNum(currentTime);
};

const changeDuration = (sound, timebar) => {
  timebar.addEventListener('input', (e) => {
    sound.currentTime = sound.duration * e.target.value;
  });
};
const controlVolume = (controller, loud, audio) => {
  controller.addEventListener('input', (e) => {
    loud = e.target.value;
    audio.volume = e.target.value;
  });
};

const onAudioLoad = (
  sound,
  loadingBox,
  player,
  durationTimeContainer,

  timebar
) => {
  showPlayer(loadingBox, player);
  durationTimeContainer.textContent = getTimeCodeFromNum(sound.duration);
  changeDuration(sound, timebar);
};

export {
  onAudioLoad,
  showPlayer,
  hiddenPlayer,
  updateTime,
  changeDuration,
  controlVolume,
  toggleBtn,
};
