const getContent = (lang) => {
  const questionsGroup =
    lang === 'en'
      ? [
          'Warmup',
          'Passerines',
          'Forest birds',
          'Songbirds ',
          'Raptors ',
          'Seabirds',
        ]
      : [
          'Разминка',
          'Воробьиные',
          'Лесные птицы',
          'Певчие птицы',
          'Хищные птицы',
          'Морские птицы',
        ];
  const startButton = lang === 'en' ? 'start' : 'Старт';
  const nextButton = lang === 'en' ? 'next level' : 'следующий уровень';

  const headerLinks = lang === 'en' ? ['Home', 'Play'] : ['Главная', 'Играть'];

  const gameDescription =
    lang === 'en'
      ? ['Listen to the player. Select a bird from the list.']
      : ['Послушайте плеер.Выберите птицу из списка.'];

  const congratulateText1 =
    lang === 'en' ? ['Сongratulations!'] : ['Поздравляем!'];

  const congratulateText2 =
    lang === 'en'
      ? [
          `<p class="results-text">
    You passed the quiz and scored <span id="results"></span> points out of 30.
  </p>`,
        ]
      : [
          ` <p class="results-text">
    Вы прошли викторину и набрали <span id="results"></span> из 30
    возможных баллов.
  </p>`,
        ];

  const startText =
    lang === 'en'
      ? 'You can click notes or birds'
      : 'Можете нажать на птичку или ноту';
  const score = lang === 'en' ? 'Score: ' : `Счёт: `;
  const againButton = lang === 'en' ? 'solve again ' : 'Пройти ещё раз';
  return [
    questionsGroup,
    startButton,
    nextButton,
    headerLinks,
    gameDescription,
    congratulateText1,
    congratulateText2,
    startText,
    score,
    againButton,
  ];
};
export { getContent };
